<template>
    <h1 class='display-3 font-weight-bold' @click="navegar">
        <header>
                <v-icon large>
                  assignment_ind
                </v-icon>
                  <span class="grey--text font-weight-light">Util</span>
                  <span>Jobs</span>
              </header>
    </h1>
</template>

<script>
export default {
  methods: {
    navegar: function () {
      var ruta = this.$router.currentRoute.fullPath
      if (ruta === '/registro' || ruta === '/home') {
        this.$router.push('/home')
      } else {
        this.$router.push('/trabajador/inicio')
      }
    }
  }
}
</script>

