              <v-card-text class=subtitle-1>
                <v-row>
                  <v-col cols=12>
                    <b class=mr-2>Zoom</b>
                    <v-btn text icon class='boton-zoom' @click="aumentarZoom">
                      <v-icon x-large>
                        mdi-magnify-plus-outline
                      </v-icon>
                    </v-btn>
                    <v-btn text icon class='boton-zoom' @click="disminuirZoom">
                      <v-icon x-large>
                        mdi-magnify-minus-outline
                      </v-icon>
                    </v-btn>
                    {{$store.state.zoom - 100 +'%'}}
                  </v-col>
                  <v-col cols=12 lg=6>
                    <div><b>Color primario:</b></div>
                    <div style='text-align: center !important'>
                      <v-color-picker style='display: inline-block;' elevation=2 v-model='$vuetify.theme.themes.light.primary'
                        dot-size="21"
                        mode="rgba"
                        swatches-max-height="211"
                      ></v-color-picker>
                    </div>
                  </v-col>
                  <v-col cols=12 lg=6>
                    <div><b>Color segundario:</b></div>
                    <div style='text-align: center !important'>
                      <v-color-picker style='display: inline-block;' elevation=2  v-model='$vuetify.theme.themes.light.secundario'
                        dot-size="21"
                        mode="rgba"
                        swatches-max-height="211"
                      ></v-color-picker>
                    </div>
                  </v-col>
                  <v-col cols=12 lg=6>
                    <div><b>Color de fondo del texto:</b></div>
                    <div style='text-align: center !important'>
                      <v-color-picker style='display: inline-block;' elevation=2  v-model='$vuetify.theme.themes.light.fondo'
                        dot-size="21"
                        mode="rgba"
                        swatches-max-height="211"
                      ></v-color-picker>
                    </div>
                  </v-col>
                  <v-col cols=12 md=6 align-self="center">
                    <div style='text-align: center !important'>
                      <v-btn
                        class="secundario white--text font-weight-bold mb-2"
                        style='display: inline-block;'
                        elevation="2"
                        large
                        @click='aparienciaDefecto'>
                        APARIENCIA POR DEFECTO
                      </v-btn>
                    </div>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-col>
        </v-row>
    </v-container>
  </div>
</template>

<script>
import BarraNavegacion from '../../components/Trabajador/barraNavegacion.vue'
import MenuPerfil from '../../components/Trabajador/menuPerfil.vue'

export default {
  name: 'InicioTrabajador',
  data: function () {
    return {
      camposTrabajador: ['Nombre*', 'Primer apellido*', 'Segundo apellido*', 'NIF*', 'Fecha de nacimiento*']
    }
  },
  components: {
    BarraNavegacion,
    MenuPerfil
  },
  methods: {
    aumentarZoom: function () {
      document.body.style.zoom = this.$store.state.zoom + 10 + '%'
      this.$store.state.zoom += 10
    },
    disminuirZoom: function () {
      document.body.style.zoom = this.$store.state.zoom - 10 + '%'
      this.$store.state.zoom -= 10
    },
    aparienciaDefecto: function () {
      document.body.style.zoom = 100 + '%'
      this.$store.state.zoom = 100
      this.$vuetify.theme.themes.light.fondo = '#e9e9e9'
      this.$vuetify.theme.themes.light.secundario = '#ff6340'
      this.$vuetify.theme.themes.light.primary = '#1976D2'
    }
  }
}
</script>

