<script>
export default {
  name: 'Home',
  data: function () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    navegarRegistro: function () {
      this.$router.push('registro')
    },
    iniciarSesion: function () {
      this.$router.push('trabajador/inicio')
      if (this.username === 'trabajador') {
        if (this.password === '1234') {
          this.$router.push('trabajador/inicio')
        }
      } else if (this.username === 'empresa') {
        if (this.password === '1234') {
          this.$router.push('empresa/inicio')
        }
      }
    }
  }
}
</script>

