<template>
<div>
  <b-navbar class="primary" sticky toggleable="lg" type="dark" variant="info">
    <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

    <b-collapse id="nav-collapse" is-nav>
      <b-navbar-nav >
        <b-nav-item :active="buscarOfertaActivo" to="/trabajador/inicio" class='boton-navegacion'>
          <v-icon large :color=buscarOfertaColor>
            mdi-briefcase-search-outline
          </v-icon>
          <b class="ml-1">Buscar Ofertas</b>
        </b-nav-item>
        <b-nav-item :active="foroActivo" to="/foro" class="ml-lg-3 boton-navegacion" >
          <v-icon large :color=foroColor>
            mdi-forum-outline
          </v-icon>
          <b class="ml-1">Foro</b>
        </b-nav-item>
        <b-nav-item :active="miPerfilActivo" to="/trabajador/perfil" class='boton-navegacion'>
          <v-icon large :color=miPerfilColor>
            perm_identity
          </v-icon>
          <b class="ml-1">Mi Perfil</b>
        </b-nav-item>
      </b-navbar-nav>

      <!-- Right aligned nav items -->
      <b-navbar-nav class="ml-auto">

        <b>
        <b-nav-item-dropdown text="Idioma" right>
          <b-dropdown-item href="#">EN</b-dropdown-item>
          <b-dropdown-item href="#">ES</b-dropdown-item>
          <b-dropdown-item href="#">RU</b-dropdown-item>
          <b-dropdown-item href="#">FA</b-dropdown-item>
        </b-nav-item-dropdown>
        </b>
        <b-nav-item to="/home" class="ml-lg-3 boton-navegacion">
          <b>Cerrar sesión</b>
        </b-nav-item>

      </b-navbar-nav>
    </b-collapse>
  </b-navbar>
</div>
</template>

<script>
export default {
  name: 'PostForo',
  data: function () {
    return {
      buscarOfertaActivo: false,
      foroActivo: false,
      miPerfilActivo: false,
      buscarOfertaColor: '',
      foroColor: '',
      miPerfilColor: ''
    }
  },
  mounted () {
    var ruta = this.$router.currentRoute.fullPath
    if (ruta === '/trabajador/inicio' || ruta === '/trabajador/resultadosBusqueda' || ruta === '/trabajador/detallesTrabajo') {
      this.buscarOfertaActivo = true
      this.buscarOfertaColor = 'white'
    } else if (ruta === '/foro' || ruta === '/postForo') {
      this.foroActivo = true
      this.foroColor = 'white'
    } else if (ruta === '/trabajador/perfil' || ruta === '/trabajador/modificarPerfil' ||
    ruta === '/trabajador/seguridad' || ruta === '/trabajador/ofertasGuardadas' || ruta === '/trabajador/apariencia') {
      this.miPerfilActivo = true
      this.miPerfilColor = 'white'
    }
  }
}
</script>

<style scoped>

.boton-navegacion:hover b, .boton-navegacion:hover i{
  color:white !important;
}
</style>
