import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Registro from '../views/Registro.vue'
import InicioTrabajador from '../views/Trabajador/Inicio.vue'
import InicioEmpresa from '../views/Empresa/Inicio.vue'
import Foro from '../views/Foro.vue'
import PostForo from '../views/PostForo.vue'
import ResultadosBusqueda from '../views/Trabajador/ResultadosBusqueda.vue'
import DetallesTrabajo from '../views/Trabajador/DetallesTrabajo.vue'
import Perfil from '../views/Trabajador/Perfil.vue'
import OfertasGuardadas from '../views/Trabajador/OfertasGuardadas.vue'
import Seguridad from '../views/Trabajador/Seguridad.vue'
import ModificarPerfil from '../views/Trabajador/ModificarPerfil.vue'
import Apariencia from '../views/Trabajador/Apariencia.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    alias: ['/home'],
    name: 'Home',
    component: Home
  }, {
    path: '/registro',
    name: 'Registro',
    component: Registro
  }, {
    path: '/trabajador/inicio',
    name: 'InicioTrabajador',
    component: InicioTrabajador
  }, {
    path: '/empresa/inicio',
    name: 'InicioEmpresa',
    component: InicioEmpresa
  }, {
    path: '/foro',
    name: 'Foro',
    component: Foro
  }, {
    path: '/postForo',
    name: 'PostForo',
    component: PostForo
  }, {
    path: '/trabajador/resultadosBusqueda',
    name: 'ResultadosBusqueda',
    component: ResultadosBusqueda
  }, {
    path: '/trabajador/detallesTrabajo',
    name: 'DetallesTrabajo',
    component: DetallesTrabajo
  }, {
    path: '/trabajador/perfil',
    name: 'Perfil',
    component: Perfil
  }, {
    path: '/trabajador/ofertasGuardadas',
    name: 'OfertasGuardadas',
    component: OfertasGuardadas
  }, {
    path: '/trabajador/seguridad',
    name: 'Seguridad',
    component: Seguridad
  }, {
    path: '/trabajador/modificarPerfil',
    name: 'ModificarPerfil',
    component: ModificarPerfil
  }, {
    path: '/trabajador/apariencia',
    name: 'Apariencia',
    component: Apariencia
  }, {
    path: '*',
    name: 'home',
    component: Home
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
